.SingleQuestion{
    width: fit-content;
    display: flexbox;
    height: fit-content;
}

.container {
    display: flex;
    margin-top: 0.25rem;
    height: 6vh;
    width: 80vw;
    background-color: gray;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    align-items: center;
    background: linear-gradient(to right, #420573, #8335B5, #9E3785, #B8603E);
}

.container p{
    display: flex;
    flex-grow: 1;
    font-size: 150%;
}

.before{
    display: flex;
    justify-content: center;
    content: '?';
    font-weight: bold;
    font-size: 150%;
    position: relative;
    align-items: center;
    background-color: white;
    color: black;
    padding: 0.5rem;
    width: 20px;
    height: 20px;
    border-radius: 50px;
    margin: 1rem;
}

.back{
    display: flex;
    content: '^';
    justify-content: end;
    align-items: center;
    margin: 1rem;
    font-size: 200%;
    transition: all 1s;
    animation-name: flipback;
    animation-duration: 0.5s;
    animation-direction:reverse;
    cursor:default;
    transition: all 1s;
    animation-fill-mode: both;
    color: black;
}

.back2{
    display: flex;
    content: '^';
    justify-content: end;
    align-items: center;
    margin: 1rem;
    font-size: 200%;
    animation-name: flip;
    animation-duration: 0.5s;
    animation-direction:normal;
    cursor:default;
    transition: all 1s;
    animation-fill-mode: both;
    color: black;
}

