@keyframes flip{
    0%{
        display: flex;
        content: '^';
        justify-content: end;
        align-items: center;
        margin: 1rem;
        font-size: 200%;
        cursor:default;
        transition: all 1s;
    }
    100%{
        display: flex;
        justify-content: left;
        transform: rotateZ(180deg);
    }
}

@keyframes flipback{
    to{
        display: flex;
        justify-content: right;
        transform: rotateZ(180deg);
    }
}

.textboxFAQ {
    color: black;
    display: flexbox;
    align-items: baseline;
    padding: 0;
    width: 80vw;
    background-color: lightgray;
    border-top-style: solid;
    border-top-width: 3px;
    border-radius: 0 0 10px 10px;
    border-bottom-width: 50px;
    background: linear-gradient(to right, #420573, #8335B5, #9E3785, #BD4618);
}

.textboxFAQ p{
    display: block;
    background: linear-gradient(#0000006E, white 10% 90%, #0000006E);
    background-color: whitesmoke;
    height: 70px;
    margin: 1rem;
    padding: 0.5rem;
    font-size: 125%;
    color: rgb(77, 76, 76);
}

.textboxFAQ p .content{
    margin: 0;
    width: fit-content;
    display: inline-flex;
    position: absolute;
}

.textboxFAQ p .mark{
    display: inline-flex;
    background-color: rgb(77, 76, 76);
    color: white;
    width: 35px;
    justify-content: center;
    align-items: center;
    height: 35px;
    font-size: 200%;
    border-radius: 50px;
    margin-right: 0.5rem;
}
